<template>
  <div>
    <b-container>
      <h1>图书管理系统</h1>
      <b-button variant="primary" @click="$router.push({ name: 'BookForm' })">添加图书</b-button>
      &nbsp;
      <b-button variant="secondary" @click="showCategoryModal = true">类型管理</b-button>
      <b-form-group label-cols-sm="3" label-size="sm">
        <b-row>
          <b-col cols="6">
            <b-input-group size="sm">
              <b-form-input v-model="filter" placeholder="标题" @keydown.enter="search"></b-form-input>
              <b-input-group-append>
                <b-button variant="outline-secondary" @click="search">搜索</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-col>
          <b-col cols="6">
            <b-input-group size="sm">
              <b-dropdown :text="dropdownCategoryText" right>
                <b-dropdown-item @click="selectedCategory({name: '类型'})"> ---</b-dropdown-item>
                <b-dropdown-item v-for="category in categories" :key="category.id" @click="selectedCategory(category)">
                  {{ category.name }}
                </b-dropdown-item>
              </b-dropdown>
              &nbsp;
              <b-dropdown :text="dropdownAuthorText" right>
                <b-dropdown-item @click="selectedAuthor({name: '作者'})"> ---</b-dropdown-item>
                <b-dropdown-item v-for="author in authors" :key="author.id" @click="selectedAuthor(author)">
                  {{ author.name }}
                </b-dropdown-item>
              </b-dropdown>
            </b-input-group>
          </b-col>
        </b-row>
      </b-form-group>
      <b-table :items="books" :fields="fields">
        <template #cell(category)="data">
          {{ data.item.category ? data.item.category.name : '' }}
        </template>
        <template #cell(authors)="data">
          {{ data.item.authors.map(author => author.name).join(', ') }}
        </template>
        <template #cell(actions)="data">
          <b-button variant="primary" @click="$router.push({ name: 'BookForm', query: { bookId: data.item.id } })">
            编辑
          </b-button>
          &nbsp;
          <b-button variant="danger" @click="showDeleteConfirmation(data.item)">删除</b-button>
        </template>
      </b-table>
      <b-pagination
          v-model="currentPage"
          :total-rows="totalBooks"
          :per-page="param.pageSize"
          align="center"
      ></b-pagination>
    </b-container>
    <b-modal v-model="showDeleteModal" title="删除书本" @ok="deleteBook()">
      确定要删除这本书吗
    </b-modal>
    <b-modal v-model="showCategoryModal" size="xl" title="类型管理" @ok="insertOrDeleteCategory()">
      <b-form-tags
          v-model="categoryTags"
          separator=" ,;"
          placeholder="输入类型用空格，逗号，分号分隔"
          duplicate-tag-text="重复类型"
          remove-on-delete=true
      ></b-form-tags>
    </b-modal>
  </div>
</template>

<script>
import axios from 'axios';

export default ({
  name: 'BookList',
  data() {
    return {
      currentPage: 1,
      totalBooks: 0,
      param: {
        page: 1,
        pageSize: 5
      },
      books: [],
      fields: [
        {key: 'id', label: 'ID'},
        {key: 'title', label: '标题'},
        {key: 'category', label: '类型'},
        {key: 'authors', label: '作者'},
        {key: 'actions', label: ''}
      ],
      filter: '',
      categories: [], // 所有类型
      authors: [], // 所有作者
      categoryTags: [],
      dropdownCategoryText: '类型',
      dropdownAuthorText: '作者',
      showDeleteModal: false,
      showCategoryModal: false,
      bookToDelete: null
    }
  },
  methods: {
    async insertOrDeleteCategory(){
      const response = await axios.post('/api/categories', this.categoryTags);
      await this.getCategories();
    },
    // 查询
    search() {
      if (this.filter) {
        this.param.title = this.filter;
      } else {
        delete this.param.title;
      }
      this.getBooksCount();
      this.getBooks(this.currentPage);
    },
    // 类型过滤
    selectedCategory(category) {
      if (category.id) {
        this.param.categoryId = category.id;
      } else {
        delete this.param.categoryId;
      }
      this.getBooksCount();
      this.getBooks(this.currentPage);
      this.dropdownCategoryText = category.name;
    },
    // 作者过滤
    selectedAuthor(author) {
      if (author.id) {
        this.param.authorId = author.id;
      } else {
        delete this.param.authorId;
      }
      this.getBooksCount();
      this.getBooks(this.currentPage);
      this.dropdownAuthorText = author.name;
    },
    async getBooks(page) {
      this.param.page = page;
      let searchParams = new URLSearchParams(this.param);
      try {
        const response = await axios.get(`/api/books?${searchParams.toString()}`);
        this.books = response.data;
      } catch (error) {
        console.error(error);
      }
    },
    // 获取书总数
    async getBooksCount() {
      let searchParams = new URLSearchParams(this.param);
      const response = await axios.get(`/api/books/count?${searchParams.toString()}`);
      this.totalBooks = response.data;
    },
    async getCategories() {
      try {
        const response = await axios.get('/api/categories');
        this.categories = response.data;
        this.categoryTags = this.categories.map(item => item.name);
      } catch (error) {
        console.error(error);
      }
    },
    async getAuthors() {
      try {
        const response = await axios.get('/api/authors');
        this.authors = response.data;
        this.authorTags = this.authors.map(item => item.name);
      } catch (error) {
        console.error(error);
      }
    },
    showDeleteConfirmation(book) {
      this.bookToDelete = book; // 要删除的book
      this.showDeleteModal = true;
    },
    async deleteBook() {
      await axios.delete(`/api/books/${this.bookToDelete.id}`, this.bookToDelete);
      await this.getBooksCount();
      await this.getBooks(this.currentPage);
      console.log('Deleted book:', this.bookToDelete);
      this.showDeleteModal = false;
    }
  },
  created() {
    this.getBooksCount();
    this.getBooks(this.currentPage);
    this.getAuthors();
    this.getCategories();
  },
  watch: {
    currentPage() {
      this.getBooks(this.currentPage);
    }
  }
});
</script>